<template>
  <transition name="cover" mode="out-in">
    <div class="bank-card-component" v-show="showChose">
      <transition name="move" mode="out-in">
        <div class="bank" v-show="showChose">
          <nav>
            <i></i>
            <h4>{{ title }}</h4>
            <i @click="closeAdd()" v-waves>×</i>
          </nav>
          <ul>
            <li
              v-for="obj in bankInfo"
              :key='obj.code'
              :class="[ obj.active ? 'active' : '']"
              @click="choose(obj)">{{ obj.bank_card ? obj.bank_name + '(' + obj.bank_card.slice(-4) + ')' : '' }}</li>
          </ul>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script>
// @ is an alias to /src
export default {
  name: 'BankCard',
  data () {
    return {
      bankInfo: []
    }
  },
  model: {
    prop: 'showChose',
    event: 'change'
  },
  props: {
    showChose: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '请选择开户银行卡'
    }
  },
  created () {
    this.init()
  },
  methods: {
    async init () {
      const result = await this.request.get('/bank')
      if (!result.code) {
        this.$router.push({ name: 'addBank' })
        this.$toast('请先绑定提现银行卡')
      }
      result.code === 1 && result.data.forEach(o => {
        o.active = 0
        o.formatting = o.bank_name + '(' + o.bank_card.slice(-4) + ')'
      })
      result.code === 1 && (this.bankInfo = result.data)
    },
    closeAdd () {
      this.$emit('change', false)
    },
    choose (obj) {
      this.bankInfo.forEach(o => {
        o.active = 0
      })
      obj.active = 1
      this.$emit('getBank', obj)
      this.$emit('change', false)
    }
  }
}
</script>

<style scope lang="scss">
  @import './index.scss';
  .cover-enter-active, .cover-leave-active {
    transition: all 0.5s;
  }
  .cover-enter, .cover-leave-active {
    opacity: 0;
  }
  .move-enter-active, .move-leave-active {
    transition: all 0.5s;
  }
  .move-enter, .move-leave-active {
    transform: translateY(6rem);
  }
</style>
