<template>
  <div class="addBank">
    <j-header title="提 现"></j-header>
    <main>
      <ul>
        <li>
          <p>用户余额</p>
          <jun-input v-model="amount"
            disabled>
          </jun-input>
        </li>
        <li>
          <p>提现金额</p>
          <jun-input v-model="form.amount"
            placeholder="请输入提现金额"
            clear>
          </jun-input>
        </li>
        <li @click="showBank = true">
          <p>提现银行卡</p>
          <jun-input v-model="bankName"
            placeholder="请输入提现银行卡"
            disabled
            clear>
          </jun-input>
        </li>
        <li>
          <p>手机号码</p>
          <jun-input v-model="form.phone"
            placeholder="请输入手机号码"
            disabled
            clear>
          </jun-input>
        </li>
        <li>
          <p>图形验证码</p>
          <jun-input v-model="imgCode"
            placeholder="请输入图形验证码">
          </jun-input>
          <img-code type='0'/>
        </li>
        <li>
          <p>手机验证码</p>
          <jun-input v-model="form.smscode"
            placeholder="请输入手机验证码">
          </jun-input>
          <send-code :data='{ type: 0, phone: form.phone, code: imgCode }'/>
        </li>
      </ul>
      <div class="notice">
        <p>提示：</p>
        <p>1.单笔提现手续费 {{ withdraw_fee }}%</p>
        <p>2.单笔最小提现￥{{ withdraw_min }}</p>
        <p>3.单日提现上限￥{{ withdraw_max }}</p>
        <p style="color:red;">4.提现到账时间：工作日（T+1）18：00前</p>
      </div>
      <button @click="confirm">确认添加</button>
    </main>
    <j-bank-card v-model="showBank" @getBank="getBank"/>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import JunInput from '@/components/JunInput'
import imgCode from '@/components/imgCode'
import sendCode from '@/components/sendCode'
import JBankCard from '@/components/JBankCard'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'addBank',
  components: {
    JHeader,
    JunInput,
    imgCode,
    sendCode,
    JBankCard
  },
  data () {
    return {
      showBank: false,
      imgCode: '',
      amount: '0.00',
      bankName: '',
      withdraw_fee: '',
      withdraw_min: '',
      withdraw_max: '',
      form: {
        amount: '',
        phone: '',
        bank_id: '',
        smscode: ''
      }
    }
  },
  computed: {
    ...mapGetters([
      'userInfo'
    ])
  },
  created () {
    // Object.keys(this.userInfo).length < 1 && this.$router.back()
    this.init()
    this.form.phone = this.userInfo.phone
    this.amount = this.userInfo.salesman * 1 === 10 ? this.userInfo.wallet : this.userInfo.balance
  },
  methods: {
    ...mapActions([
      'GetUserInfo'
    ]),
    init () {
      this.request.get('/config').then(res => {
        res.data.forEach(e => {
          if (e.name === 'withdraw_max') {
            this.withdraw_max = e.value
          } else if (e.name === 'withdraw_fee') {
            this.withdraw_fee = e.value
          } else if (e.name === 'withdraw_min') {
            this.withdraw_min = e.value
          }
        })
      })
    },
    getBank (obj) {
      this.bankName = obj.formatting
      this.form.bank_id = obj.id
    },
    async confirm () {
      if (this.form.amount < 100 || !this.form.amount) {
        this.$toast('单笔最小提现￥100！')
      } else if (!this.form.bank_id) {
        this.$toast('请选择银行卡！')
      } else if (!this.form.smscode) {
        this.$toast('请输入短信验证码！')
      } else {
        this.request({
          url: '/withdraw',
          method: 'post',
          data: this.form
        }).then(res => {
          this.$toast({
            message: res.message,
            position: 'top'
          })
          res.code === 1 && this.$router.push({ name: 'accountrecord' })
        })
      }
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
